<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="show">
      <c-header :title="$t('base.ViolationOfTravelRank')" isBackNav></c-header>
      <hs-scroll class="table-view">
        <ul>
          <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell-header">
                <p>{{$t('base.ThePeopleOfViolatedTravelRank')}}: <span v-for="(item, index) in details.GuestNames" :key="index">{{item.Name}}<template v-if="details.GuestNames && details.GuestNames.length > 1">、</template></span></p>
                <p>{{$t('base.PleaseChooseTheReason')}}:</p>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section>
            <hs-cell-1
              v-for="(item, index) in details.ReasonCodes" :key="index"
              :value="item.ReasonDescription"
              logoName="select.png"
              :showLogo="selectedIndex === index"
              @click.native="selectedIndex = index"></hs-cell-1>
            <hs-cell-1 v-if="details.IsDisplayOtherItem || !details.ReasonCodes || details.ReasonCodes.length === 0" logoName="select.png" :showLogo="selectedIndex === undefined" @click.native="selectedIndex = undefined">
              <div class="other-reason" :style="{width: selectedIndex === undefined ? 'calc(100% - 30px)' : '100%'}">
                <textarea v-model="otherReason" :placeholder="$t('base.OtherReasons')"></textarea>
              </div>
            </hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>
      <div class="footer">
        <hs-button width="80%" height="40px" @click="_nextStep">{{$t('base.NextStep')}}</hs-button>
      </div>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
// 选择违反差旅职级原因页面
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsButton from 'base/hs-button/hs-button'
import {mapGetters, mapMutations} from 'vuex'
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    details: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  watch: {
    otherReason(val) {
      !this.noValue(val) && (this.selectedIndex = undefined)
    }
  },
  computed: {
    ...mapGetters([
      'hotelOrder'
    ])
  },
  data() {
    return {
      selectedIndex: 0,
      otherReason: ''
    }
  },
  methods: {
    ...mapMutations([
      'SET_HOTEL_ORDER'
    ]),
    _close() {
      this.$emit('update:show', false)
    },
    _nextStep() {
      if (this.noValue(this.otherReason.trim()) && (this.selectedIndex === undefined)) {
        this.$toast(this.$t('TM.PleaseSelectOrFillInTheReasonForViolatingTheTravelRank'))
        return
      } else if (this.otherReason.length > 50) {
        this.$toast(this.$t('TM.TheLengthOfTheViolationOfTheTravelRankCannotExceedXWords'))
        return
      }
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      let ViolationRankName = ''
      this.details.GuestNames && (ViolationRankName = this.details.GuestNames.map(v => v.Name).join('、'))
      res.ViolationRankName = ViolationRankName
      res.ViolationRankReason = this.selectedIndex === undefined ? `${this.$t('base.Other')}${this.otherReason}` : this.details.ReasonCodes[this.selectedIndex].ReasonDescription
      this.SET_HOTEL_ORDER(res)
      this._close()
      this.$emit('delegate')
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1,
    HsButton
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  background-color $color_white
  z-index 200
  .table-view
    width 100%
    height calc(100% - 45px - 50px)
    overflow hidden
    background-color $color_white
    .custom-cell-header
      width 100%
      padding 10px 0
      line-height 1.2em
    .other-reason
      width 100%
      padding 15px 0
      >textarea
        width 100%
        height 80px
        background-color $color_white
        font-size 1rem
        padding 10px
        border 1px solid $color_gray_layer
        border-radius 2px
        resize none
        outline none
        box-shadow 0px 0px 0px rgba(0, 0, 0, 0)
        -webkit-appearance none
  >.footer
    @extend .flex-center
    width 100%
    height 50px
    background-color $color_white
    border-top 1px solid $color_gray_layer
    padding 0 15px
</style>



