<template>
  <div class="hs-net">
    <template v-if="noData">
      <div class="logo" style="background-image: url('static/image/base/null/no-net.png')"></div>
      <p>{{title}}</p>
      <hs-gradient-button @click="$emit('delegate')"
        style="margin-top: 20px"
        type="common"
        :title="$t('base.TryAgain')"></hs-gradient-button>
    </template>
  </div>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
export default {
  props: {
    title: {
      type: String,
      default: Vue.prototype.$t('base.TheNetworkSignalIsPoor')
    }
  },
  computed: {
    noData() {
      return !Vue.prototype.$loading.isLoading()
    }
  },
  components: {
    HsGradientButton
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-net
  @extend .flex-column
  @extend .flex-start
  width 100%
  height 100%
  font-size 0.8rem
  >.logo
    width 150px
    height 150px
    background-size 100% 100%
    margin-top 150px
  >p
    margin-top 20px
    color #7886A1
</style>



