<template>
  <div class="hs-container">
    <c-header class="c-header" :title="`${$t('base.HotelBooking')}-${Number(hotelOrder.TravelType) === 0 ? $t('base.Business') : $t('base.Personal')}`" isBackNav></c-header>

    <hs-net v-if="noValue(JSON.stringify(details))" @delegate="_getDetails"></hs-net>
    <template v-else>
      <hs-scroll
        v-if="!noValue(JSON.stringify(hotel))"
        class="table-view">
        <ul>
          <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell-hotel">
                <p class="title">{{hotel.HotelName}}</p>
                <p class="little-title">{{ratePlan.RatePlanName}}</p>
                <p>{{room.BedType}} | {{$t('base.CanAccommodateXPeople', 'function', room.Capcity)}} | {{ratePlan.BreakFast}}</p>
                <p>{{$t('base.CheckInDate')}} {{$t(hotelOrder.CheckInDate, 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}}</p>
                <p>{{$t('base.CheckOutDate')}} {{$t(hotelOrder.CheckOutDate, 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}} {{$t('base.TotalNights', 'function', getApartDayCount(hotelOrder.CheckInDate, hotelOrder.CheckOutDate))}}</p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell-cancel">
                <div style="background-image: url('static/image/hotel/irreversible.png')"></div>
                <div>
                  <p>{{ratePlan.CancelRuleDesc}}</p>
                </div>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell-room">
                <div @click="showRoomAmount = !showRoomAmount">
                  <div :type="language">
                    <p>{{$t('base.NumberOfRooms')}}</p>
                    <p>{{hotelOrder.RoomAmount}}{{$t('base.QuantifierHotel')}}<span>{{$t('base.CanAccommodateUpToXPeople', 'function', room.Capcity)}}</span></p>
                  </div>
                  <div class="arrow" :style="{transform: `rotate(${showRoomAmount ? '90deg' : '-90deg'})`}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
                </div>
                <div v-if="showRoomAmount">
                  <p v-for="n in 4" :key="n" :class="{active: hotelOrder.RoomAmount === n}" @click="_setRoomAmount(n)">{{n}}</p>
                </div>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell-people">
                <p>{{$t('base.Roomer')}}</p>
                <div>
                  <p>{{$t('base.EachRoomNeedsToFillIn', 'function', room.Capcity > 1 ? `1~${room.Capcity}` : '1')}}</p>
                  <hs-button type="common" fontSize="0.8rem" @click="$refs.CPassenger.open()">{{$t('base.Add')}} +</hs-button>
                </div>
              </div>
            </hs-cell-1>
            <hs-cell-1 v-for="(item, index) in hotelOrder.Guests" :key="index">
              <div class="people-infor" :style="{transform: `translateX(${!hotelOrder.Guests[index].openDelete ? '0px' : '-65px'})`}">
                <div class="left" @click="_setPassengerOpenDelete(true, index)">
                  <div class="logo" style="background-image: url('static/image/plane-ticket/book/delete.png')"></div>
                </div>
                <div class="right" @click="_gotoPassengerDetails(index)">
                  <div class="item">
                    <p class="name" v-if="item.DefaultNameType === 0">{{item.ChName}}</p>
                    <p class="name" v-else>{{item.LastName}}&nbsp;{{item.FirstName}}</p>
                  </div>
                  <div class="item">
                    <p class="other-title">{{$t('base.CellphoneNumber')}}</p>
                    <p class="other-value">{{`${item.Mobile ? item.Mobile : $t('base.Unfilled')}`}}</p>
                  </div>
                  <div class="delete" @click.stop="_deletePassenger(index)">{{$t('base.Delete')}}</div>
                </div>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="details.ChargeInfo">
              <div class="service-fee">
                <p>{{$t('base.ServiceFee')}}</p>
                <p>￥{{details.ChargeInfo.ServiceCharge}}</p>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section>
            <hs-cell-small-title @click.native="showArrivalTimeDialog = true"
              :title="$t('base.ArrivalHotelTime')"
              type="disabled-input"
              :value="hotelOrder.ArrivalTime"
              :placeholder="`(${$t('base.NotNecessaryChoose')}) ${$t('base.PleaseChoose')}`">
            </hs-cell-small-title>
            <hs-cell-small-title
              :title="$t('base.SpecificRequirements')"
              type="input"
              v-model="specificRequirements"
              :placeholder="`(${$t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`"
              :showLogo="false">
            </hs-cell-small-title>
            <hs-cell-small-title v-if="details.IsDisplayCustomItem" @click.native="showCCustomItem = true"
              :title="details.CustomItemName"
              type="disabled-input"
              :value="hotelOrder.CustomItem ? hotelOrder.CustomItem.Name : ''"
              :placeholder="`(${details.IsRequiredCustomItem ? $t('base.RequiredChoose') : $t('base.NotNecessaryChoose')}) ${$t('base.PleaseChoose')}`">
            </hs-cell-small-title>
            <hs-cell-small-title v-if="details.IsDisplayPurpose"
              :title="$t('base.Purpose')"
              type="input"
              v-model="purpose"
              :placeholder="`(${details.IsRequiredPurpose ? $t('base.RequiredFill') : $t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`"
              :showLogo="false">
            </hs-cell-small-title>
            <!-- TODO: 酒店授权码填写后台支持, 但是订单详情接口没有此字段, 所以暂时隐藏 -->
            <!-- <hs-cell-3 v-if="details.IsDisplayAuthorizationCode" title="授权码" :showLogo="false" titleColor="#333333">
              <input maxlength='50' style="width: 100%" v-model="authorizationCode" :placeholder="`(${details.IsRequiredAuthorizationCode ? '必填': '非必填'}) 请填写`"/>
            </hs-cell-3> -->
            <hs-cell-small-title v-if="details.VettingType === 3 && details.EnableVetting" @click.native="picker.show()"
              :title="$t('base.Approver')"
              type="disabled-input"
              :value="hotelOrder.AppointVettingPersonName"
              :placeholder="`(${$t('base.RequiredChoose')}) ${$t('base.PleaseChoose')}`">
            </hs-cell-small-title>
            <hs-cell-small-title v-if="Number(hotelOrder.TravelType) === 0" @click.native="payTypePicker.show()"
              :title="$t('base.PaymentWay')"
              type="disabled-input"
              :value="hotelOrder.PayType | filterPayType"
              :placeholder="`(${$t('base.RequiredChoose')}) ${$t('base.PleaseChoose')}`">
            </hs-cell-small-title>
          </hs-section>
          <hs-section>
            <hs-cell-1 :value="$t('base.ContactInformation')" logoName="add.png" @click.native="$refs.CFrequentContacts.open()"></hs-cell-1>
            <hs-cell-1 v-for="(item, index) in hotelOrder.Contacts" :key="index">
              <div class="people-infor" :style="{transform: `translateX(${!hotelOrder.Contacts[index].openDelete ? '0px' : '-65px'})`}">
                <div class="left" @click="_setContactsOpenDelete(true, index)">
                  <div class="logo" style="background-image: url('static/image/plane-ticket/book/delete.png')"></div>
                </div>
                <div class="right" @click="_gotoContactsDetails(index)">
                  <div class="item">
                    <p class="name">{{item.Name}}</p>
                  </div>
                  <div class="item">
                    <p class="other-title">{{$t('base.CellphoneNumber')}}</p>
                    <p class="other-value">{{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                  </div>
                  <div class="item">
                    <p class="other-title">{{$t('base.Email')}}</p>
                    <p class="other-value">{{item.Email ? item.Email : $t('base.Unfilled')}}</p>
                  </div>
                  <div class="delete" @click.stop="_deleteContact(index)">{{$t('base.Delete')}}</div>
                </div>
              </div>
            </hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>
      <div class="footer" @click="_switchDetailsDialog">
        <div class="price">
          <p v-if="details.ChargeInfo">￥{{details.ChargeInfo.TotalPrice}}</p>
          <div class="logo" :style="{transform: showDetailsDialog ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
        </div>
        <div class="details">
          <hs-gradient-button @click="_gotoConfirm"
            type="common"
            width="auto"
            :title="$t('base.NextStep')">
          </hs-gradient-button>
        </div>
      </div>
    </template>

    <hs-dialog v-model="showDetailsDialog" height="calc(100% - 50px)" ref="detailsDialog">
      <hs-scroll class="detailsDialog" v-if="details.ChargeInfo" :data="[hotelOrder]" :isListenScroll="true" @scrollStart="_scrollStart" @click.native.stop>
        <ul>
          <hs-cell-1 :showLogo="false">
            <div class="detailsDialog-custom-cell">
              <p>{{$t('base.PriceHotel')}}</p>
              <p>
                <span>￥{{details.ChargeInfo.AverageRate * getApartDayCount(hotelOrder.CheckInDate, hotelOrder.CheckOutDate)}}</span>
                <span>x {{getApartDayCount(hotelOrder.CheckInDate, hotelOrder.CheckOutDate)}}{{$t('base.QuantifierNight')}}</span>
                <span>x {{hotelOrder.RoomAmount}}{{$t('base.QuantifierHotel')}}</span>
              </p>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false">
            <div class="detailsDialog-custom-cell">
              <p>{{$t('base.ServiceFee')}}</p>
              <p><span>￥{{details.ChargeInfo.ServiceCharge}}</span></p>
            </div>
          </hs-cell-1>
        </ul>
      </hs-scroll>
      <div class="show-more" ref="show-more">{{$t('base.SlideUpToSeeMore')}}</div>
    </hs-dialog>
    <hs-dialog v-model="showArrivalTimeDialog">
      <hs-scroll class="arrivalTimeDialog" :data="arrivalTimeItems" ref="arrivalTimeDialog">
        <ul>
          <div class="item-header">{{$t('base.ETA')}}</div>
          <div class="item-header-little">{{$t('base.TheRoomIsReservedAllNight')}}</div>
          <div class="items">
            <p
              v-for="(item, index) in arrivalTimeItems" :key="index"
              @click="_selectArrivalTimeItem(item)"
              :class="{actived: hotelOrder.ArrivalTime === item}"
              :style="{'margin-right': index % 3 === 2 ? '0px' : '', 'margin-bottom': index % 3 === 0 ? '20px' : ''}">{{item}}</p>
          </div>
        </ul>
      </hs-scroll>
    </hs-dialog>

    <template v-if="room.Capcity">
      <c-passenger
        ref="CPassenger"
        :XOrder="hotelOrder"
        :BookInit="details"
        :OrderBusinessType="2"
        :MaxLength="hotelOrder.RoomAmount * room.Capcity"
        @delegateWithConfirm="_delegateWithConfirm"
      ></c-passenger>
    </template>

    <passenger-details
      ref="PassengerDetails"
      :details="editingPassenger"
      :BookInit="details"
      :OrderBusinessType="2"
      @delegate="_delegateWithPassengerDetails"
    ></passenger-details>

    <c-custom-item :show.sync="showCCustomItem" :orderInit.sync="details" @delegate="_delegateWithCustomItem"></c-custom-item>
    <c-contacts :show.sync="showCContacts" v-model="contactDetails" :datalist="hotelOrder.Contacts" :OrderBusinessType="2" @delegate="_delegateWithContacts"></c-contacts>
    <c-frequent-contacts ref="CFrequentContacts" :orderInit.sync="details" :datalist="hotelOrder.Contacts" :OrderBusinessType="2" @delegate="_delegateWithFrequentContacts"></c-frequent-contacts>
    <violation-rank :show.sync="showViolationRank" :details="violationRankDetails" @delegate="_nextStep"></violation-rank>
  </div>
</template>
<script type="text/ecmascript-6">
import CContacts from 'components/c-passenger/c-contacts'
import CCustomItem from 'components/c-passenger/c-custom-item'
import CFrequentContacts from 'components/c-passenger/c-frequent-contacts'
import CHeader from 'components/c-header/c-header'
import CPassenger from 'components/c-passenger/c-passenger'
import HsButton from 'base/hs-button/hs-button'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import HsNet from 'base/hs-null/hs-net'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import PassengerDetails from 'pages/personal/passengers/details.vue'
import ViolationRank from './violation-rank'
import Vue from 'vue'
import {kpiHotelGetBookInitInfo, kpiHotelCheckVetting, kpiHotelCheckReasonCode, kpiHotelReCalculateFee, kpiHotelCheckOrderRepeat} from 'api'
import {mapGetters, mapMutations} from 'vuex'
const PAY_TYPE_PICKER_DATA_1 = { text: Vue.prototype.$t('base.CompanyPayment'), value: 1 } // eslint-disable-line
const PAY_TYPE_PICKER_DATA_2 = { text: Vue.prototype.$t('base.PersonalPayment'), value: 2 } // eslint-disable-line
const PAY_TYPE_PICKER_DATA_3 = { text: `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`, value: 3 } // eslint-disable-line
export default {
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // 限制只能由路由'/hotel/inforDetails'进入这个页面
      if (from.path !== '/hotel/inforDetails') {
        next(false)
      } else {
        next()
      }
    })
  },
  beforeRouteLeave (to, from, next) {
    if (this.$refs.CPassenger && this.$refs.CPassenger.show) {
      this.$refs.CPassenger.close()
      next(false)
    } else if (this.$refs.PassengerDetails && this.$refs.PassengerDetails.show) {
      this.$refs.PassengerDetails.close()
      next(false)
    } else {
      this.showViolationRank = false
      this.showCCustomItem = false
      this.showCContacts = false
      next()
    }
  },
  created() {
    if (this.noValue(this.hotelOrder.TravelType)) {
      this.$router.replace('/home')
      return
    }

    // 重置酒店订单中一些内容
    this._resetContentInTheHotelOrder()

    // 初始化数据
    this._getDetails()

    // 初始化到达时间数据
    this._initArrivalTimeItems()
  },
  computed: {
    ...mapGetters([
      'hotelOrder'
    ]),
    hotel() {
      // console.log('hotel: ', this.$route.params.hotel)
      return this.$route.params.hotel
    },
    room() {
      // console.log('room: ', this.$route.params.room)
      return this.$route.params.room
    },
    ratePlan() {
      // console.log('ratePlan: ', this.$route.params.ratePlan)
      return this.$route.params.ratePlan
    },
    // 差旅目的
    purpose: {
      get: function() {
        return this.hotelOrder.Purpose
      },
      set: function(val) {
        let res = JSON.parse(JSON.stringify(this.hotelOrder))
        res.Purpose = val
        this.SET_HOTEL_ORDER(res)
      }
    },
    // 授权码
    authorizationCode: {
      get: function() {
        return this.hotelOrder.AuthorizationCode
      },
      set: function(val) {
        let res = JSON.parse(JSON.stringify(this.hotelOrder))
        res.AuthorizationCode = val
        this.SET_HOTEL_ORDER(res)
      }
    },
    specificRequirements: {
      get: function() {
        return this.hotelOrder.SpecificRequirements
      },
      set: function(val) {
        let res = JSON.parse(JSON.stringify(this.hotelOrder))
        res.SpecificRequirements = val
        this.SET_HOTEL_ORDER(res)
      }
    }
  },
  watch: {
    showArrivalTimeDialog(val) {
      if (val) {
        this._initArrivalTimeItems()
      }
    }
  },
  filters: {
    filterPayType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.CompanyPayment')
        case 2:
          return Vue.prototype.$t('base.PersonalPayment')
        case 3:
          return `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  data() {
    return {
      details: {},
      /** 价格明细 */
      showDetailsDialog: false,

      /** 房间数 */
      showRoomAmount: true,

      /** 到达时间 */
      showArrivalTimeDialog: false,
      arrivalTimeItems: [],

      /** 自定义项 */
      showCCustomItem: false,

      /** 联系人 */
      showCContacts: false,
      contactDetails: {},

      /** 违反差旅职级 */
      showViolationRank: false,
      violationRankDetails: {},

      /** 常旅客 */
      editingPassenger: {}
    }
  },
  methods: {
    ...mapMutations([
      'SET_HOTEL_ORDER'
    ]),
    _getDetails() {
      let params = {
        HotelID: this.hotelOrder.HotelID,
        CheckInDate: this.beTimeNumber(this.hotelOrder.CheckInDate),
        CheckOutDate: this.beTimeNumber(this.hotelOrder.CheckOutDate),
        RoomID: this.hotelOrder.RoomID,
        RatePlanID: this.hotelOrder.RatePlanID,
        TravelType: this.hotelOrder.TravelType,
        OrderType: this.hotelOrder.OrderType
      }
      kpiHotelGetBookInitInfo(params).then((res) => {
        console.log('获取酒店订单填写初始信息: ', res)
        this.details = res.ResultData
        // 默认联系人
        if (!this.noValue(JSON.stringify(this.details.DefaultContacts))) {
          let res = JSON.parse(JSON.stringify(this.hotelOrder))
          res.Contacts = [Object.assign(this.details.DefaultContact, {openDelete: false})]
          // res.Contacts = this.details.DefaultContacts.map(v => {
          //   v.openDelete = false
          //   return v
          // })
          this.SET_HOTEL_ORDER(res)
        }
        // =====================================================
        this._creatPayTypePicker(this.details.PayTypes)
        this._creatApproversPicker(this.details.Approvers)
        this._setRoomAmount(this.hotelOrder.RoomAmount)
      })
    },
    _initArrivalTimeItems() {
      let today = this.beTime(new Date())
      let time = this.beTime(new Date(), 'hh:mm')
      let CheckInDate = this.hotelOrder.CheckInDate
      let isToday = today === CheckInDate
      let res = [
        '14:00', '15:00', '16:00', '17:00', '18:00',
        '19:00', '20:00', '21:00', '22:00', '23:00',
        '23:59', `${this.$t('base.Tomorrow', 'function', 1)}`, `${this.$t('base.Tomorrow', 'function', 2)}`, `${this.$t('base.Tomorrow', 'function', 3)}`, `${this.$t('base.Tomorrow', 'function', 4)}`,
        `${this.$t('base.Tomorrow', 'function', 5)}`, `${this.$t('base.Tomorrow', 'function', 6)}`
      ]
      isToday && (res = res.filter(v => time < v))
      this.arrivalTimeItems = res
      if (res[0] > this.hotelOrder.ArrivalTime) {
        this._selectArrivalTimeItem(res[0])
      }
    },
    // 审批人picker
    _creatApproversPicker(Approvers) {
      let data = []
      if (Approvers) {
        Approvers.forEach(element => {
          data.push({
            text: element.Name,
            value: element.ID
          })
        })
      }
      this.picker = this.$createPicker({
        title: this.$t('base.SelectApprover'),
        data: [data],
        cancelTxt: this.$t('base.Cancel'),
        confirmTxt: this.$t('base.Confirm'),
        onSelect: this._selectApproversPickerHandle
      })
    },
    _selectApproversPickerHandle(val, index, text) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.AppointVettingPersonID = val[0]
      res.AppointVettingPersonName = text[0]
      this.SET_HOTEL_ORDER(res)
    },
    // =========================================
    // 初始化支付方式picker
    _creatPayTypePicker(PayTypes) {
      let data = []
      if (Number(this.hotelOrder.TravelType) === 1) {
        data = [PAY_TYPE_PICKER_DATA_2]
      } else if (PayTypes) {
        PayTypes.forEach(element => {
          if (element === 1) {
            data.push(PAY_TYPE_PICKER_DATA_1)
          } else if (element === 2) {
            data.push(PAY_TYPE_PICKER_DATA_2)
          } else if (element === 3) {
            data.push(PAY_TYPE_PICKER_DATA_3)
          }
        })
      }
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.PayType = data[0] ? data[0].value : null
      this.SET_HOTEL_ORDER(res)
      this.payTypePicker = this.$createPicker({
        title: this.$t('base.PaymentWay'),
        data: [data],
        cancelTxt: this.$t('base.Cancel'),
        confirmTxt: this.$t('base.Confirm'),
        onSelect: this._payTypePickerSelectHandle
      })
    },
    _payTypePickerSelectHandle(val, index, text) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.PayType = val[0]
      this.SET_HOTEL_ORDER(res)
    },
    // ===========================================================
    // 费用明细
    _switchDetailsDialog() {
      if (this.showDetailsDialog) {
        this.$refs.detailsDialog.showDetails = false
      } else {
        console.log(this.$refs.detailsDialog)
        this.showDetailsDialog = true
        setTimeout(() => {
          let dom = this.$refs.detailsDialog.$children[0].scroll
          if (dom.wrapperHeight < dom.scrollerHeight) {
            this.$refs['show-more'].style.opacity = 1
          } else {
            this.$refs['show-more'].style.opacity = 0
          }
        }, 400)
      }
    },
    // 设置房间数
    _setRoomAmount(n) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.RoomAmount = n
      this.SET_HOTEL_ORDER(res)
      let params = {
        HotelID: this.hotelOrder.HotelID,
        CheckInDate: this.beTimeNumber(this.hotelOrder.CheckInDate),
        CheckOutDate: this.beTimeNumber(this.hotelOrder.CheckOutDate),
        RoomID: this.hotelOrder.RoomID,
        RatePlanID: this.hotelOrder.RatePlanID,
        RoomAmount: this.hotelOrder.RoomAmount,
        TravelType: this.hotelOrder.TravelType,
        OrderType: this.hotelOrder.OrderType
      }
      kpiHotelReCalculateFee(params).then((res) => {
        console.log('酒店重新计算价格: ', res)
        this.details.ChargeInfo = res.ResultData
      })
    },
    /**
     * 常旅客列表回调
     * 用于设置当前订单的乘客信息
     */
    _delegateWithConfirm(datalist) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.Guests = datalist
      this.SET_HOTEL_ORDER(res)
    },
    _setPassengerOpenDelete(value, index) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.Guests[index].openDelete = value
      this.SET_HOTEL_ORDER(res)
    },
    _gotoPassengerDetails(index) {
      if (this.hotelOrder.Guests[index].openDelete) {
        this._setPassengerOpenDelete(false, index)
      } else {
        this.editingPassenger = JSON.parse(JSON.stringify(this.hotelOrder.Guests[index]))
        this.$set(this.editingPassenger, 'index', index)
        this.$refs.PassengerDetails.open()
      }
    },
    _deletePassenger(index) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.Guests.splice(index, 1)
      this.SET_HOTEL_ORDER(res)
    },
    // 修改入住人回调
    _delegateWithPassengerDetails(passenger) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.Guests.splice(passenger.index, 1, passenger)
      this.SET_HOTEL_ORDER(res)
    },
    // 选择预计到达时间
    _selectArrivalTimeItem(item) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.ArrivalTime = item
      this.SET_HOTEL_ORDER(res)
    },
    // 选择自定义项
    _delegateWithCustomItem(item) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.CustomItem = item
      this.SET_HOTEL_ORDER(res)
    },
    // 保存联系人回调
    _delegateWithContacts(contacts) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.Contacts.splice(contacts.index, 1, Object.assign(contacts, {openDelete: false}))
      this.SET_HOTEL_ORDER(res)
    },
    _setContactsOpenDelete(value, index) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.Contacts[index].openDelete = value
      this.SET_HOTEL_ORDER(res)
    },
    _gotoContactsDetails(index) {
      if (this.hotelOrder.Contacts[index].openDelete) {
        this._setContactsOpenDelete(false, index)
      } else {
        this.contactDetails = Object.assign(JSON.parse(JSON.stringify(this.hotelOrder.Contacts[index])), {index: index})
        this.showCContacts = true
      }
    },
    _deleteContact(index) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.Contacts.splice(index, 1)
      this.SET_HOTEL_ORDER(res)
    },
    _delegateWithFrequentContacts(val) {
      console.log('选中的联系人: ', val)
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.Contacts = val
      this.SET_HOTEL_ORDER(res)
    },
    // ===================================================================
    _scrollStart() {
      this.$refs['show-more'].style.opacity = 0
    },
    // 确定
    _gotoConfirm() {
      console.log(this.hotelOrder)
      if (this.noValue(JSON.stringify(this.details))) {
        this.$toast(this.$t('TM.TheSysteIsBusyPleaseReOperate'))
        this.$router.replace('/home')
        return
      }
      if (this.hotelOrder.Guests.length === 0) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.Roomer')))
        return
      } else if (this.hotelOrder.Guests.length < this.hotelOrder.RoomAmount) {
        this.$toast(this.$t('TM.ChooseAtLeastXPeople', 'function', this.hotelOrder.RoomAmount))
        return
      } else if (this.details.IsRequiredCustomItem && this.noValue(JSON.stringify(this.hotelOrder.CustomItem))) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.details.CustomItemName))
        return
      } else if (this.hotelOrder.Contacts.length === 0) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.Contacts')))
        return
      } else if (this.details.IsRequiredPurpose && this.noValue(this.hotelOrder.Purpose)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.Purpose')))
        return
      } else if (this.details.VettingType === 3 && this.details.EnableVetting && this.noValue(this.hotelOrder.AppointVettingPersonName)) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.Approver')))
        return
      } else if (this.details.IsRequiredAuthorizationCode && this.noValue(this.hotelOrder.AuthorizationCode)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.AuthorizationCode')))
        return
      } else if (this.noValue(this.hotelOrder.PayType)) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.PaymentWay')))
        return
      } else {
        for (let item of this.hotelOrder.Contacts) {
          if (this.noValue(item.Mobile)) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.CellphoneNumber')}))
            return
          } else if (this.noValue(item.Email) && (item.IsSendBookEmail || item.IsSendIssuedEmail || item.IsSendConfirmEmail)) {
            this.$toast(this.$t('TM.PleaseFillX', 'function', {name: item.Name, thing: this.$t('base.Email')}))
            return
          }
        }
        let maplist = new Map()
        if (this.hotelOrder.Contacts.filter(a => !maplist.has(a.Mobile) && maplist.set(a.Mobile, 1)).length < this.hotelOrder.Contacts.length) {
          this.$toast(this.$t('TM.ThereMustBeNoContactWithTheSamePhoneNumber'))
          return
        }
      }
      /** 填写姓名 */
      let hotelOrder = JSON.parse(JSON.stringify(this.hotelOrder))
      hotelOrder.Guests.forEach((element, index) => {
        hotelOrder.Guests[index].Name = element.DefaultNameType === 0 ? element.ChName : `${element.LastName} ${element.FirstName}`
      })
      this.SET_HOTEL_ORDER(hotelOrder)

      if (Number(this.hotelOrder.TravelType) === 0) {
        this._checkVetting()
      } else {
        this._nextStep()
      }
    },
    // 校验审批
    _checkVetting() {
      let params = {
        HotelID: this.hotelOrder.HotelID,
        CheckInDate: this.beTimeNumber(this.hotelOrder.CheckInDate),
        CheckOutDate: this.beTimeNumber(this.hotelOrder.CheckOutDate),
        RoomID: this.hotelOrder.RoomID,
        RatePlanID: this.hotelOrder.RatePlanID,
        CheckGuests: this.hotelOrder.Guests,
        CustomItem: this.hotelOrder.CustomItem,
        AppointVettingPersonID: this.hotelOrder.AppointVettingPersonID,
        AppointVettingPersonName: this.hotelOrder.AppointVettingPersonName,
        PayType: this.hotelOrder.PayType,
        TravelType: Number(this.hotelOrder.TravelType)
      }
      kpiHotelCheckVetting(params).then((res) => {
        console.log('校验审批: ', res)
        if (res.ResultData.IsNeedVetting === true && res.ResultData.IsSameVetting === false) {
          this.$alert({
            content: this.$t('base.IfTheApproverIsInconsistent')
          })
        } else {
          if (res.ResultData.IsNeedVetting === true) {
            let hotelOrder = JSON.parse(JSON.stringify(this.hotelOrder))
            hotelOrder.VettingProcessList = res.ResultData.VettingProcessList
            this.SET_HOTEL_ORDER(hotelOrder)
          }
          this._checkReason()
        }
      })
    },
    // 检查原因
    _checkReason() {
      let params = JSON.parse(JSON.stringify(this.hotelOrder))
      params.CheckGuests = this.hotelOrder.Guests
      params.CheckInDate = this.beTimeNumber(this.hotelOrder.CheckInDate)
      params.CheckOutDate = this.beTimeNumber(this.hotelOrder.CheckOutDate)
      kpiHotelCheckReasonCode(params).then((res) => {
        console.log('获取酒店reasoncode: ', res)
        if (res.ResultData.IsViolate === true) {
          let names = ''
          if (res.ResultData.GuestNames) {
            res.ResultData.GuestNames.forEach(element => {
              if (element.ViolateRankResult === 2) {
                names += `${element.Name}、`
              }
            })
          }
          if (!this.noValue(names)) {
            names = names.substring(0, names.length - 1)
            this.$toast(this.$t('TM.XIsNotAllowedToBook', 'function', names))
          } else {
            this.showViolationRank = true
            this.violationRankDetails = res.ResultData
          }
        } else {
          this._checkOrderRepeat()
        }
      })
    },
    /**
     * 检验酒店重复下单
     */
    _checkOrderRepeat() {
      let params = {
        CheckInDate: this.beTimeNumber(this.hotelOrder.CheckInDate),
        CheckOutDate: this.beTimeNumber(this.hotelOrder.CheckOutDate),
        Guests: this.hotelOrder.Guests
      }
      kpiHotelCheckOrderRepeat(params).then((res) => {
        console.log('检验酒店重复下单: ', res)
        if (res.ResultData.IsRepeat) {
          this.$alert({
            content: res.ResultData.Message,
            cancelTxt: this.$t('base.Cancel'),
            confirmTxt: this.$t('base.Continue'),
            confirmFn: () => {
              this._nextStep()
            }
          })
        } else {
          this._nextStep()
        }
      })
    },
    /**
     * 下一步
     */
    _nextStep() {
      this.$router.push({name: 'hotelOrderConfirm', params: Object.assign(this.$route.params, {hotelOrderInit: this.details})})
    },
    /**
     * 重置酒店订单中一些内容
     */
    _resetContentInTheHotelOrder() {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.ViolationRankName = ''
      res.ViolationRankReason = ''
      res.Guests = []
      this.SET_HOTEL_ORDER(res)
    }
  },
  components: {
    CContacts,
    CCustomItem,
    CFrequentContacts,
    CHeader,
    CPassenger,
    HsButton,
    HsCell1,
    HsCellSmallTitle,
    HsDialog,
    HsGradientButton,
    HsNet,
    HsScroll,
    HsSection,
    PassengerDetails,
    ViolationRank
  }
}
</script>
